import React, { Component } from "react";
import { Router } from "@reach/router";
import Observable from "./model/cacheObservable";
import { Toast } from "antd-mobile";
import Loadable from "react-loadable";
import Loading from "loading/lottie";

const DetailIntroduce = Loadable({
  loader: () => import("./DetailIntroduce/view"),
  loading: () => <Loading />
});
const ShoppingSelect = Loadable({
  loader: () => import("./shoppingSelect/wrapperComponent"),
  loading: () => <Loading />
});
const ServerContent = Loadable({
  loader: () => import("./serverContent/serverList"),
  loading: () => <Loading />
});
const UserEvaluate = Loadable({
  loader: () => import("./userEvaluate/evaluateCache"),
  loading: () => <Loading />
});

export default class extends Component {
  state = {
    CacheState: null,
    CacheId: null
  };
  constructor() {
    super(...arguments);
    this.getObservable = Observable();
    this.getObservable.outState.subscribe({
      next: val => this.setState(val),
      complete: () => console.log("Complete!"),
      error: val => Toast.info(val)
    });
  }
  componentWillUnmount() {
    this.getObservable.putState.unsubscribe();
  }
  render() {
    return (
      <div>
        <Router path="detail">
          <DetailIntroduce
            path="item/:url"
            CacheObservable={this.getObservable.putState}
            {...this.state}
            requestOptions={{
              url: "/item/detail?id=",
              method: "GET"
            }}
          />
          <ServerContent
            path="server/:url"
            CacheObservable={this.getObservable.putState}
            CacheState={this.state.CacheState}
            CacheId={this.state.CacheId}
            requestOptions={{
              url: "/item/detail?id=",
              method: "GET"
            }}
          />
          <ShoppingSelect
            path="select/:url"
            CacheState={this.state.CacheState}
            CacheId={this.state.CacheId}
            selectHistory={this.state.selectHistory}
            CacheObservable={this.getObservable.putState}
            requestOptions={{
              url: "/item/detail?id=",
              method: "GET"
            }}
          />
          <UserEvaluate path="comment/:url" />
        </Router>
      </div>
    );
  }
}



